import React, { Component } from "react";
import { NavLink, Switch, Route, Link } from "react-router-dom";
import UserDetail from "./UserDetail";
import NewsList from "./NewsList";

export class Dashboard extends Component {
  render() {
    let id = 3;
    const { match } = this.props;
    return (
      <div className="row">
        <div className="col-3" style={{ backgroundColor: "#f4f4f4" }}>
          <br />
          <nav className="nav flex-column">
            <NavLink className="nav-link" to={`${match.url}`}>
              Home
            </NavLink>
            <NavLink className="nav-link " to={`${match.url}/users`}>
              Users
            </NavLink>
            <NavLink className="nav-link" to={`${match.url}/news`}>
              News
            </NavLink>
          </nav>
        </div>
        <div className="col-9 mt-3">
          <Switch>
            <Route path={`${match.path}`} exact>
              Hello Dashboard Home
            </Route>
            <Route path={`${match.path}/users`} exact>
              Hello User
            </Route>

            <Route path={`${match.path}/users/:id`} component={UserDetail} />

            <Route path={`${match.path}/news`} component={NewsList} />
          </Switch>
        </div>
      </div>
    );
  }
}

export default Dashboard;
